@use "reset.css";
@use "_helpers.scss";
@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono&display=swap");
:root {
  --bg-color: #111013;
}
html,
body {
  height: 100%;
  background-color: var(--bg-color);

  box-sizing: border-box;
  font-family: "Roboto Mono", monospace;
  color: #fff;
}

.section {
  height: 100%;
  box-sizing: border-box;
}
.home {
  background: radial-gradient(
    50% 50% at 50% 50%,
    rgba(217, 217, 217, 0) 11.98%,
    rgba(217, 217, 217, 0.15) 97.92%
  );
  .content {
    @extend %centering;
    justify-content: space-around;
    flex-direction: column;
    height: 100%;
    position: relative;
    #butterfly {
      position: absolute;
      top: 0;
      left: 0;
      width: 20px;
      fill: #957ec4c0;
      .left_wing,
      .right_wing,
      .body {
        transform-origin: center;
      }
    }
    .logo_container {
      width: 90%;
      max-width: 400px;
      display: flex;
      flex-direction: column;
      align-items: center;
      gap: 35px;
    //   transform: translateX(-10px);
    }
    .bakery_name_container {
      width: 80%;
      max-width: 300px;
      transform: translateX(20px);
    }
    .choix_parfum {
      @extend %centering;
      flex-direction: column;
      gap: 15px;
      .choix_container {
        @extend %centering;
        gap: 15px;
        .parfum {
          cursor: pointer;
          width: 40px;
          transition: scale 1s ease;
          
          &:hover {
              transform: scale(1.1) !important;
            }
            > img {
              
            width: 100%;
            object-fit: contain;
          }
        }
      }
    }
  }
}
